import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Layout, PageHeader, Button, message, Modal, Table, } from 'antd';
import { AdvanceFilter, Icon } from "@components";
import Apis from '@/apis'
import EditForm from './edit'
const { Content } = Layout;
const confirm = Modal.confirm;
const initData = { title: '', pId: '', url: '', icon: '', path: '', moduleName: '', sortIndex: 0 }

@inject(({ appStore, authStore }) => ({
  setting: appStore.setting,
  permissions: authStore.permissions
}))
@observer
class ListView extends Component {
  editform = null
  state = {
    visible: false,
    editItem: {},
    treeData: [],
  }

  showModal = item => {
    this.setState({
      visible: true,
      editItem: { ...initData, ...item }
    });
    if (this.editform) {
      this.editform.setFieldsValue({
        ...initData,
        ...item
      })
    }
  };

  hideModal = () => {
    this.setState({
      visible: false,
    });
  };

  okModal = () => {
    if (this.editform) {
      const { editItem } = this.state
      let newData = this.editform.getFieldsValue()
      newData.moduleName = newData.moduleName || ''
      Apis[editItem._id ? 'menuUpdate' : 'menuCreate']({
        _id: editItem._id, ...newData
      }).then(res => {
        if (res) {
          message.success('操作成功!')
          this.fetchData()
        } else {
          message.error('操作失败!')
        }
        this.setState({
          visible: false
        });
      })
    }
  };

  async componentDidMount() {
    this.fetchData()
  }

  fetchData(params = {}) {
    Apis.menuList({ _limit: 200, ...params, }).then(res => {
      if (Array.isArray(res)) {
        function loop(pid) {
          let children = res.filter(val => val.pId === pid) || [];
          children.forEach(item => {
            item.id = item._id
            item.key = item._id
            item.value = item._id
            let _res = loop(item._id);
            if (_res.length) item.children = _res;
          });
          children.sort((a, b) => {
            return a.sortIndex - b.sortIndex;
          });
          return children;
        }
        this.setState({
          treeData: loop("")
        })
      }
    })
  }

  handleRemove = (_id, e) => {
    confirm({
      title: `你确定要删除所选菜单吗?`,
      okText: '确定',
      cancelText: '取消',
      onOk: () => {
        Apis.menuRemove({ _id, }).then(res => {
          if (!res.state) {
            message.success('删除成功!')
            this.fetchData()
          } else {
            message.error('删除失败!')
          }
        })
      },
    });
  }

  render() {
    const { treeData, editItem, visible, } = this.state
    // const { permissions } = this.props
    const columns = [
      {
        title: '标题',
        dataIndex: 'title',
        component: 'input'
      },
      {
        title: '图标',
        dataIndex: 'icon',
      },
      {
        title: '链接',
        dataIndex: 'url',
      },
      {
        title: '排序',
        dataIndex: 'sortIndex',
      },
      {
        title: '操作',
        width: 300,
        render: val => (
          <div>
            {<Button type="link" size="small" onClick={this.showModal.bind(this, { ...initData, pId: val._id })}>
              <Icon iconName="Add" marginRight={5} />{`添加子菜单`}
            </Button>}
            {<Button type="link" size="small" onClick={this.showModal.bind(this, val)}>
              <Icon iconName="Edit" marginRight={5} />编辑
            </Button>}
            {<Button type="link" size="small" onClick={this.handleRemove.bind(this, val._id)}>
              <Icon iconName="Delete" marginRight={5} />删除
            </Button>}
          </div>
        )
      },
    ];

    return (
      <Content>
        <PageHeader
          ghost={true}
          onBack={false}
          title={`系统菜单管理`}
          className="page-title"
          extra={[
            <Button key="1" type="primary" onClick={this.showModal.bind(this, null)}>
              <Icon iconName="Add" marginRight={5} />{`创建菜单`}
            </Button>,
          ]}
        />
        {/* {permissions['manageMenu/list'] ?  */}
        <>
          <AdvanceFilter
            showAdvance
            onSubmit={values => {
              if (values.title) values.title = { 'contains': values.title }
              this.fetchData(values)
            }}
            columns={columns}
          />
          <Content>
            <div className="app-content">
              {treeData.length ? <Table
                columns={columns}
                size="small"
                pagination={{
                  pageSize: this.props.setting.pageSize
                }}
                dataSource={treeData}
                // defaultExpandAllRows
                rowKey={record => record._id}
              /> : null}
            </div>
          </Content>
        </>
        {/* : <Alert
          message="信息提示"
          description="没有权限查看内容, 请联系管理员."
          type="warning"
          showIcon
          style={{margin: '0 20px'}}
        />} */}
        <Modal
          title={`${editItem._id && !editItem.pId ? '编辑' : '创建'}菜单`}
          visible={visible}
          onOk={this.okModal}
          onCancel={this.hideModal}
          okText="确认"
          cancelText="取消"
          width={700}
        >
          <EditForm data={editItem} treeData={treeData} ref={el => this.editform = el} />
        </Modal>
      </Content>
    )
  }
};

export default ListView;
