import React, { forwardRef } from "react";
import { Form, Input, InputNumber, TreeSelect } from 'antd';
// import { Selector } from '@/components';

export default forwardRef((props, ref) => {
  const [form] = Form.useForm();
  const { treeData = [], data = {} } = props
  const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 18 },
  };
  return (
    <Form {...formItemLayout} form={form} ref={ref} initialValues={data}>
      <Form.Item label="父节点" name="pId">
        <TreeSelect treeData={treeData} />
      </Form.Item>
      <Form.Item label="标题" name="title" rules={[{ required: true, message: '请输入标题', }]}>
        <Input />
      </Form.Item>
      <Form.Item label="图标" name="icon">
        <Input />
      </Form.Item>
      <Form.Item label="链接" name="url">
        <Input />
      </Form.Item>
      {/* <Form.Item label="视图" name="view">
        <Selector
          api="dictionaryOptions"
          params={{ _id: '6087f9ee36ce3c4e47e509db' }}
          titlepropname="key"
          valuepropname="value"
        />
      </Form.Item> */}
      <Form.Item label="权限" name="path">
        <Input />
      </Form.Item>
      <Form.Item label="模块" name="moduleName">
        <Input />
      </Form.Item>
      <Form.Item label="排序" name="sortIndex">
        <InputNumber />
      </Form.Item>
    </Form>
  );
})